@mixin lineDec {
  content: '';
  width: 100%;
  height: 1px;
  background-color: #581700;
}

.container {
  position: relative;
    z-index: 1;
}

.tlt {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #003650;
  &:before {
      @include lineDec;
  }
  &:after {
      @include lineDec;
  }
}
